import { makeStyles } from '@fluentui/react-components';
import '../styles/estilo.css';

const useStyles = makeStyles({
  root: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    width: "80%",
    textAlign: "center",
    '@media (max-width: 1024px)': {
      width: "90%",
      marginTop: "1em"
    },
    '@media (max-width: 768px)': {
      width: "95%",
      marginTop: "1em"
    },
    '@media (max-width: 480px)': {
      width: "100%",
      marginTop: "1em"
    }
  },
  marca: {
    fontSize: "4rem",
    fontWeight: "bold",
    lineHeight: "1.2",
    margin: 0,
    '@media (max-width: 1024px)': {
      fontSize: "3.5rem",
    },
    '@media (max-width: 768px)': {
      fontSize: "3rem",
    },
    '@media (max-width: 480px)': {
      fontSize: "2.5rem",
    }
  }
});


export const Marca = () => {
  const styles = useStyles();
  const subtitulo: string = "Temporada 2023-2024";

  return (
    <>
      <div className={styles.root} id="cabeceraPC">
        <h1 className={styles.marca}>Super Padel Bros Tour <span className="signature">2</span></h1>
        <p className="lead mb-3" id="estado">{subtitulo}</p>
      </div>
    </>
  )
}