import * as React from "react";
import {
  TableBody,
  TableCell,
  TableRow,
  Table,
  TableHeader,
  TableHeaderCell,
  TableCellLayout,
} from "@fluentui/react-components";
import { ClasificacionProps, IPareja, ITablaClasificacion } from "../utils/Interfaces";
import { makeStyles } from "@fluentui/react-components";

const useStyles = makeStyles({
  titulo: {
    fontWeight: "normal",
    fontSize: "2em",
    '@media (max-width: 1024px)': {
      fontSize: "1.8em",
    },
    '@media (max-width: 768px)': {
      fontSize: "1.5em",
    },
    '@media (max-width: 480px)': {
      fontSize: "1.2em",
    }
  },
  partidosGanados: {
    '@media (max-width: 1024px)': {
      display: "none"
    },
    '@media (max-width: 768px)': {
      display: "none"
    },
    '@media (max-width: 480px)': {
      display: "none"
    }
  },
  movil: {
    display: "none",
    '@media (max-width: 1024px)': {
      display: "table-row"
    },
    '@media (max-width: 768px)': {
      display: "table-row"
    },
    '@media (max-width: 480px)': {
      display: "table-row"
    }
  },
  columnaPosicion: {
    width: "15%"
  },
  columnaPareja: {
    width: "50%"
  }
});


export const Clasificacion = (props: ClasificacionProps) => {
  const styles = useStyles();
  const [items, setItems] = React.useState<ITablaClasificacion[]>([]);
  const columns = [
    { columnKey: "posicion", label: "Posición" },
    { columnKey: "pareja", label: "Pareja" },
    { columnKey: "partidosGanados", label: "PG" },
    { columnKey: "puntos", label: "Puntos" },
  ];

  React.useEffect(() => {
    if (props.parejas && props.parejas.length > 0) {
      let posicion = 1;
      const nuevosItems: ITablaClasificacion[] = props.parejas.map((pareja: IPareja) => ({
        posicion: posicion++,
        pareja: pareja.name,
        partidosGanados: pareja.partidosGanados,
        puntos: pareja.puntos
      }));

      nuevosItems.sort((a, b) => b.puntos - a.puntos);

      setItems(nuevosItems);
    }
  }, [props.parejas]);

  return (
    <>
      <h2 className={styles.titulo}>Clasificación</h2>
      <Table arial-label="Default table">
        <TableHeader>
          <TableRow className={styles.partidosGanados}>
            {columns.map((column) => (
              <TableHeaderCell key={column.columnKey}>
                {column.label}
              </TableHeaderCell>
            ))}
          </TableRow>
          <TableRow className={styles.movil}>
            <TableHeaderCell key="posicion" className={styles.columnaPosicion}>
              Posición
            </TableHeaderCell>
            <TableHeaderCell key="pareja" className={styles.columnaPareja}>
              Pareja
            </TableHeaderCell>
            <TableHeaderCell key="puntos">
              Puntos
            </TableHeaderCell>
          </TableRow>
        </TableHeader>
        <TableBody>
          {items.map((item: ITablaClasificacion, key: number) => (
            <TableRow key={key}>
              <TableCell>
                <TableCellLayout>
                  {key + 1}
                </TableCellLayout>
              </TableCell>
              <TableCell>
                <TableCellLayout>
                  {item.pareja}
                </TableCellLayout>
              </TableCell>
              <TableCell className={styles.partidosGanados}>
                {item.partidosGanados}
              </TableCell>
              <TableCell>
                <TableCellLayout>
                  {item.puntos === 0 ? "0" : item.puntos}
                </TableCellLayout>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </>
  );
};
