import { ICalendario, IJugador, IPareja, IPartidosJugados } from "./Interfaces"

const URL_Partidos: string = "https://api.jsonbin.io/v3/b/66ec975fe41b4d34e4336cfc";
const URL_Jugadores: string = "https://api.jsonbin.io/v3/b/66ec9744e41b4d34e4336cef";
const URL_Parejas: string = "https://api.jsonbin.io/v3/b/66ec9716ad19ca34f8a92ccd";
const URL_Calendario: string = "https://api.jsonbin.io/v3/b/66ec96dae41b4d34e4336cc4";
const cabecera = {
  method: 'GET',
  headers: {
    'X-Access-Key': '$2a$10$iWOUmaXMkjQ8W34pkiAlUeATTQsXZazHJAmoOg6ncLs3mVD.1iZfO',
    'Content-Type': 'application/json'
  }
}

export const getJsonParejas = async (): Promise<IPareja[]> => {
  return new Promise((resolve) => {
    fetch(URL_Parejas, cabecera)
      .then((response) => response.json())
      .then((jsonParejas) => {
        const parejas: IPareja[] = [];

        jsonParejas.map((pareja: any) => {
          let nuevaPareja: IPareja = {
            id: pareja["idPareja"],
            name: pareja["pareja"],
            imagen: pareja["imagen"],
            jugadores: pareja["jugadores"],
            partidosGanados: pareja["partidosGanados"],
            puntos: pareja["puntos"],
            flavorText: pareja["texto"]
          }

          parejas.push(nuevaPareja);
        })

        resolve(parejas);
      })
      .catch((error) => console.error('Error:', error))
  })
}

export const getJsonCalendario = async (): Promise<ICalendario[]> => {
  return new Promise((resolve) => {
    fetch('https://aitorsanrod.github.io/jsonfiles/calendario.json')
      .then((response) => response.json())
      .then((jsonCalendario) => {
        const calencario: ICalendario[] = [];

        jsonCalendario.map((jornada: any) => {
          let nuevaJornada: ICalendario = {
            jornada: jornada["jornada"],
            partidos: jornada["partidos"],
            fecha: jornada["fecha"],
          }

          calencario.push(nuevaJornada);
        })

        resolve(calencario);
      })
      .catch((error) => console.error('Error:', error))
  })
}

export const getJsonJugadores = async (): Promise<IJugador[]> => {
  return new Promise((resolve) => {
    fetch('https://aitorsanrod.github.io/jsonfiles/jugadores.json')
      .then((response) => response.json())
      .then((jsonJugadores) => {
        const jugadores: IJugador[] = [];

        jsonJugadores.map((jugador: any) => {
          let nuevoJugador: IJugador = {
            nombre: jugador["nombre"],
            id: jugador["id"],
            puntos: jugador["puntos"],
            partidosJugados: jugador["partidosJugados"],
            partidosGanados: jugador["partidosGanados"]
          }

          jugadores.push(nuevoJugador);
        })

        resolve(jugadores);
      })
      .catch((error) => console.error('Error:', error))
  })
}

export const getJsonPartidos = async (): Promise<IPartidosJugados[]> => {
  return new Promise((resolve) => {
    fetch('https://aitorsanrod.github.io/jsonfiles/partidos_2023-2024.json')
      .then((response) => response.json())
      .then((jsonPartidos) => {
        const partidos: IPartidosJugados[] = [];

        jsonPartidos.map((jugador: any) => {
          let nuevoPartido: IPartidosJugados = {
            jornada: jugador["jornada"],
            parejaLocal: jugador["pareja1"],
            puntosParejaLocal: jugador["puntosPareja1"],
            parejaVisitante: jugador["pareja2"],
            puntosParejaVisitante: jugador["puntosPareja2"],
            set1: jugador["set1"],
            set2: jugador["set2"],
            set3: jugador["set3"]
          }

          partidos.push(nuevoPartido);
        })

        resolve(partidos);
      })
      .catch((error) => console.error('Error:', error))
  })
}
