import { Divider, FluentProvider, makeStyles, tokens, webDarkTheme } from "@fluentui/react-components"
import React from "react";
import { getJsonCalendario, getJsonJugadores, getJsonParejas, getJsonPartidos } from "../utils/JsonBinApiGetters";
import { ICalendario, IJugador, IPareja, IPartidosJugados } from "../utils/Interfaces";
import { Header } from "./Header";
import { Clasificacion } from "./Clasificacion";
import { Ranking } from "./Ranking";
import { Calendario } from "./Calendario";
import { Parejas } from "./Parejas";
import { MenuMovil } from "./MenuMovil";

const useStyles = makeStyles({
  root: {
    display: "flex",
    justifyContent: "start",
    alignItems: "center",
    flexDirection: "column",
    margin: "2em",
    '@media (max-width: 1024px)': {
      margin: "1.5em",
    },
    '@media (max-width: 768px)': {
      margin: "1em",
    },
    '@media (max-width: 480px)': {
      margin: "0.5em",
    }
  },
  separador: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyItems: "center",
    minHeight: "96px",
    backgroundColor: tokens.colorNeutralBackground1,
    width: "100%",
    '@media (max-width: 1024px)': {
      minHeight: "80px",
    },
    '@media (max-width: 768px)': {
      minHeight: "64px",
    },
    '@media (max-width: 480px)': {
      minHeight: "48px",
    }
  },
  customWidth: {
    width: "80%",
    '@media (max-width: 1024px)': {
      width: "90%",
    },
    '@media (max-width: 768px)': {
      width: "95%",
    },
    '@media (max-width: 480px)': {
      width: "100%",
    }
  },
  contenido: {
    display: "flex",
    justifyContent: "start",
    alignItems: "center",
    flexDirection: "column",
    width: "70%",
    '@media (max-width: 1024px)': {
      width: "80%",
    },
    '@media (max-width: 768px)': {
      width: "90%",
    },
    '@media (max-width: 480px)': {
      width: "100%",
    }
  }
});

export const Home = () => {
  const [parejas, setParejas] = React.useState<IPareja[]>([]);
  const [jugadores, setJugadores] = React.useState<IJugador[]>([]);
  const [calendario, setCalendario] = React.useState<ICalendario[]>([]);
  const [partidos, setPartidos] = React.useState<IPartidosJugados[]>([]);
  const [showPortada, setShowPortada] = React.useState<boolean>(true);
  const [showRanking, setShowRanking] = React.useState<boolean>(false);
  const [showCalendario, setShowCalendario] = React.useState<boolean>(false);
  const [showParejas, setShowParejas] = React.useState<boolean>(false);
  const styles = useStyles();

  //#region UseEffects
  React.useEffect(() => {
    getParejas();
    getCalendario();
    getJugadores();
    getPartidos();
  }, []);
  //#endregion UseEffects

  //#region Functions
  const getParejas = async () => {
    const jsonParejas: IPareja[] = await getJsonParejas();

    setParejas(jsonParejas);
  };

  const getJugadores = async () => {
    const jsonJugadores: IJugador[] = await getJsonJugadores();

    setJugadores(jsonJugadores);
  };

  const getCalendario = async () => {
    const jsonCalendario: ICalendario[] = await getJsonCalendario();

    setCalendario(jsonCalendario);
  };

  const getPartidos = async () => {
    const jsonPartidosJugados: IPartidosJugados[] = await getJsonPartidos();

    setPartidos(jsonPartidosJugados);
  }

  const changeVisibilityPortada = () => {
    setShowPortada(true);
    setShowRanking(false);
    setShowCalendario(false);
    setShowParejas(false);
  }

  const changeVisibilityRanking = () => {
    setShowPortada(false);
    setShowRanking(true);
    setShowCalendario(false);
    setShowParejas(false);
  }

  const changeVisibilityCalendario = () => {
    setShowPortada(false);
    setShowRanking(false);
    setShowCalendario(true);
    setShowParejas(false);
  }

  const changeVisibilityParejas = () => {
    setShowPortada(false);
    setShowRanking(false);
    setShowCalendario(false);
    setShowParejas(true);
  }
  //#endregion Functions

  return (
    <FluentProvider theme={webDarkTheme}>
      <div className={styles.root}>
        <Header
          changeVisibilityPortada={changeVisibilityPortada}
          changeVisibilityRanking={changeVisibilityRanking}
          changeVisibilityCalendario={changeVisibilityCalendario}
          changeVisibilityParejas={changeVisibilityParejas}
        />
        <div className={styles.separador}>
          <Divider className={styles.customWidth} />
        </div>
        <div className={styles.contenido}>
          {showPortada && <Clasificacion parejas={parejas} />}
          {showRanking && <Ranking />}
          {showCalendario && <Calendario />}
          {showParejas && <Parejas />}
        </div>
      </div>
    </FluentProvider>
  )
}