import { makeStyles } from "@fluentui/react-components";
import { Marca } from "./Marca";
import { BotonMenu } from "./BotonMenu";
import { IDatosMenu, IHeader } from "../utils/Interfaces";
import '../styles/estilo.css';
import { MenuMovil } from "./MenuMovil";

const useStyles = makeStyles({
  root: {
    display: "flex",
    justifyContent: "space-evenly",
    alignItems: "center",
    gap: "2em",
    width: "70%",
    '@media (max-width: 1024px)': {
      width: "80%",
      gap: "1.5em",
    },
    '@media (max-width: 768px)': {
      width: "90%",
      flexDirection: "column",
      gap: "1em",
    },
    '@media (max-width: 480px)': {
      width: "100%",
      flexDirection: "column",
      gap: "0.5em",
    }
  },
  botonera: {
    display: "flex",
    justifyContent: "center",
    gap: "2em",
    width: "65%",
    '@media (max-width: 1024px)': {
    },
    '@media (max-width: 768px)': {
      display: "none"
    },
    '@media (max-width: 480px)': {
      display: "none"
    }
  },
  marca: {
    width: "35%",
    '@media (max-width: 1024px)': {
      width: "40%",
    },
    '@media (max-width: 768px)': {
      width: "100%",
    },
    '@media (max-width: 480px)': {
      width: "100%",
    }
  },
  menuMovil: {
    display: "none",
    position: "fixed",
    top: "0",
    left: "0",
    zIndex: "99",
    '@media (max-width: 1024px)': {
      display: "block",
      position: "fixed",
      top: "0",
      left: "0",
      zIndex: "99",
    },
    '@media (max-width: 768px)': {
      display: "block",
      position: "fixed",
      top: "0",
      left: "0",
      zIndex: "99",
    },
    '@media (max-width: 480px)': {
      display: "block",
      position: "fixed",
      top: "0",
      left: "0",
      zIndex: "99",
    }
  }
});

export const Header = ({ changeVisibilityPortada, changeVisibilityRanking, changeVisibilityCalendario, changeVisibilityParejas }: IHeader) => {
  const linkPortada: string = "https://res.cloudinary.com/dodq6s1fh/image/upload/v1686249249/SPBT2/hogar_skqqnv.png";
  const linkRanking: string = "https://res.cloudinary.com/dodq6s1fh/image/upload/v1686249249/SPBT2/trofeo_ui4ond.png";
  const linkCalendario: string = "https://res.cloudinary.com/dodq6s1fh/image/upload/v1686249249/SPBT2/calendario-reloj_uocicx.png";
  const linkParejas: string = "https://res.cloudinary.com/dodq6s1fh/image/upload/v1686249249/SPBT2/bola_zh2new.png";
  const datosMenuPC: IDatosMenu[] = [
    {
      titulo: "Portada",
      enlace: linkPortada,
      setVisibility: changeVisibilityPortada
    },
    {
      titulo: "Ranking",
      enlace: linkRanking,
      setVisibility: changeVisibilityRanking
    },
    {
      titulo: "Calendario",
      enlace: linkCalendario,
      setVisibility: changeVisibilityCalendario
    },
    {
      titulo: "Equipos",
      enlace: linkParejas,
      setVisibility: changeVisibilityParejas
    },
  ];
  const styles = useStyles();

  return (
    <div className={styles.root} id="cajaMenu">
      <div className={styles.marca}>
        <Marca />
      </div>
      <div className={styles.menuMovil}>
        <MenuMovil datosMenu={datosMenuPC} />
      </div>
      <div className={styles.botonera}>
        {
          datosMenuPC.map((data, idx) => {
            return (<BotonMenu nombre={data.titulo} imagen={data.enlace} key={idx} setVisibility={data.setVisibility} />)
          })
        }
      </div>
    </div>
  )
}