import { Image } from "react-bootstrap";
import { IBotonMenu } from "../utils/Interfaces";
import '../styles/estilo.css';
import { makeStyles } from "@fluentui/react-components";

const useStyles = makeStyles({
  boton: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    borderRadius: "8px",
    padding: "5px 3em",
    textDecoration: "none",
    '@media (max-width: 768px)': {
      padding: "5px 2em",
    },
    '@media (max-width: 480px)': {
      padding: "5px 1em",
      flexDirection: "row",
    }
  },
  cajon: {
    '@media (max-width: 768px)': {
      transform: "scale(0.5)",
    },
    '@media (max-width: 480px)': {
      transform: "scale(0.5)",
    }
  },
  texto: {
    textAlign: "center",
    marginTop: "5px !important",
    marginBottom: "5px",
    fontSize: "1.5em",
    '@media (max-width: 768px)': {
      display: "none",
      fontSize: "1.2em",
    },
    '@media (max-width: 480px)': {
      display: "none",
      fontSize: "1em",
      marginTop: "3px !important",
      marginBottom: "3px",
    }
  }
});

export const BotonMenu = ({ nombre, imagen, setVisibility }: IBotonMenu) => {
  const styles = useStyles();

  return (
    <div className={styles.cajon} onClick={setVisibility}>
      <a href="#" className={styles.boton}>
        <Image src={imagen} className="img-fluid" alt="" />
        <p className={styles.texto}>{nombre}</p>
      </a>
    </div>
  )
}
